.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 1px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser{
    padding: 15px 20px;
    height: 20px;
    background-color: rgb(193, 193, 193);
    display: flex;
    align-items: center;
    display: flex;
    justify-content: space-between;
    position: sticky;
    z-index: 2;
}

.circeles{
    display: flex;
}

.github-logo{
    display: flex;
    align-items: center;
    justify-content: center;
}

.github-logo > a{
    color: black;
    width: 25px;
    height: 25px;
    transition: 0.5s ease;
}

.github-logo > a:hover{
    color: white;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: rgb(253, 250, 250);
}

.p-img{
    width: 100%;
    transition: all 10s ease;
}

.p:hover .p-img{
    transform: translateY(-83%);
}