.Intro {
    display: flex;
    height: 100vh;
}

.left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

}

.right {
    flex: 1;
    position: relative;
}

.left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 40px;
}

.i-title{
    height: 60px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }

}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b356;
    display: flex;
    align-items: center;
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.i-img{
    width: 60%;
    height: 60%;
    object-fit: cover;
    position: absolute;
    top: 20%;
    left: 30%;
    border-radius: 10px;
}

.i-bg{
    clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 40% 100%, 0% 50%, 40% 0);
    background-color: #59b356;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}